*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
}

ul{
    list-style-type: none;
}

li{
    list-style-type: none;
}

a{
    text-decoration: none;
}

.container{
    max-width: 90vw;
    margin: 0 auto;
}